<template>
    <div class="list-container">
      <!-- 列表内容 -->
      <div class="grid-container">
        <div v-for="item in paginatedItems" :key="item.id" class="grid-item">
            <img :src="require(`@/assets/files.png`)" alt="icon" class="item-icon" />
            <h4>{{ item.title }}</h4>
            <p class="file-count">{{ item.fileCount }} files</p>
        </div>
      </div>
      
      <!-- 分页组件 -->
      <div class="pagination">
        <button @click="prevPage" :disabled="currentPage === 1">Prev</button>
        <span>Page {{ currentPage }} of {{ totalPages }}</span>
        <button @click="nextPage" :disabled="currentPage === totalPages">Next</button>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        items: [
          { id: 1, icon: 'path/to/icon1.png', title: 'Lecture Recording', fileCount: 10 },
          { id: 2, icon: 'path/to/icon2.png', title: 'Lecture Notes', fileCount: 5 },
          { id: 3, icon: 'path/to/icon3.png', title: 'SWEN90006', fileCount: 12 },
          { id: 4, icon: 'path/to/icon4.png', title: '2024Semester2', fileCount: 2 },
          { id: 5, icon: 'path/to/icon5.png', title: 'Assignments', fileCount: 3 },
          { id: 6, icon: 'path/to/icon6.png', title: 'Workshop', fileCount: 4 },
          // 添加更多数据项
        ],
        currentPage: 1,
        itemsPerPage: 16, 
      };
    },
    computed: {
      paginatedItems() {
        const start = (this.currentPage - 1) * this.itemsPerPage;
        const end = start + this.itemsPerPage;
        return this.items.slice(start, end);
      },
      totalPages() {
        return Math.ceil(this.items.length / this.itemsPerPage);
      },
    },
    methods: {
      prevPage() {
        if (this.currentPage > 1) {
          this.currentPage--;
        }
      },
      nextPage() {
        if (this.currentPage < this.totalPages) {
          this.currentPage++;
        }
      },
    },
  };
  </script>
  
